import axios from "axios";
import { NextRequest } from "next/server";
import type { Note, NoteResponse } from "../../types/note";
import type { User } from "../../types/user";
import { cookies } from "next/headers";
import nextServer from "next/dist/server/next-server";

const BASE_REMOTE = "https://notehub-api.goit.study";

function createRemoteInstance(cookies?: string) {
  return axios.create({
    baseURL: BASE_REMOTE,
    headers: {
      Cookie: cookies ?? "",
    },
    withCredentials: true,
  });
}

const cookieHeaders = async () => {
  const cookieStore = await cookies();
  return {
    Cookie: cookieStore.toString(),
  };
};

interface FetchNotesParams {
  page?: number;
  perPage?: number;
  search?: string;
  tag?: string;
}


export async function serverFetchNotes(req: NextRequest, params?: FetchNotesParams): Promise<NoteResponse> {
  const inst = createRemoteInstance(req.headers.get("cookie") ?? "");
  const { data } = await inst.get<NoteResponse>("/notes", { params });
  return data;
}

export async function checkSession(req: NextRequest): Promise<User | null> {
  const inst = createRemoteInstance(req.headers.get("cookie") ?? "");
  const { data } = await inst.get<User>("/auth/session");
  return data || null;
}

export const getMe = async (): Promise<User> => {
  const headers = await cookieHeaders();
  const { data } = await nextServer.get<User>('/users/me', { headers });
  return data;
};


export async function serverFetchNoteById(req: NextRequest, id: string): Promise<Note> {
  const inst = createRemoteInstance(req.headers.get("cookie") ?? "");
  const { data } = await inst.get<Note>(`/notes/${id}`);
  return data;
}
